.features{
  // max-width: 1180px;
  margin: 60px auto 0;
  //@extend %defaultSection;

  .tab &{
    @extend %defaultSection;
  }

  > div{
      padding: 0 40px;
  }


  > div > .flex,
  > .flex{
    margin: 0 -40px;
    flex-wrap: wrap;
  }

}

.feature{
  color: $cWhite;
  width: 33%;
  padding: 0 40px;

  margin-bottom: 40px;
  min-height: 210px;
  p {
    color: currentColor;
  }

  h4 {
    margin: 0;
    height: 60px;
    font-size: em(22px);
    line-height: 27px;
    color: currentColor;
  }

  @include mm {
    h4 {
      height: auto;
    }
  }

  @include sm {
    margin-bottom: 30px;
    min-height: auto;
  }

  p{
    font-size: em(14px);
    color: $cWhite;
  }

  @include responsive('1130px', 'min'){
    h4{
      height: auto;
    }
  }

  svg:not(.notes){
    fill: $cWhite;
  }

  svg.notes{
    fill:transparent;
    stroke: $cWhite;
    stroke-width: 2;
  }

  .ico-features.notes{

  }

  .blue &{
    color: $cWhite;

    svg{
      fill: $cWhite;
    }
  }


  @include sm {
    width: 100%;
    padding: 0;
    
    h4 {
      height: auto;
    }

  }


}



